@import 'shared.scss';

/* LOGOWALL */
.logoWall {
  display: flex;
  flex-wrap: wrap;

  > div {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content:flex-start;
    box-sizing: border-box;

    padding-right: 40px;

    &:first-child {
      width: 42.5%;
      background: turqoise;
    }
    &:nth-child(2) {
      width: 32.5%;
    }
    &:last-child {
      width: 25%;
      padding-right: 0;
    }

    > * {
      align-self: baseline;
    }
  }

  h4 {
    width: 100%;
    padding-bottom: 20px;
  }
}

.image {
  width: 210px;
  height: auto;
  margin-right: 15px;
  margin-bottom: 15px;
}

.smallImage {
  width: 125px;
  height: auto;
  margin-right: 15px;
  margin-bottom: 15px;
}

/* RESPONSIVE */
// 2 columns
@include mqSmallerEqual($bpL) {
  .logoWall > div {
    width: 50% !important;
    &:first-child {
      padding-right: 20px;
    }
    &:nth-child(2) {
      padding-left: 20px;
      padding-right: 0;
    }

    &:last-child {
      margin-top: 35px;
    }
  }
}
@include mqSmallerEqual($bpM) {
  .smallImage {
    width: 100px;
  }

  .image {
    width: 180px;
  }
}
// 1 column
@include mqSmallerEqual($bpXS) {
  .logoWall > div {
    padding: 0 !important;
    width: 100% !important;

    &:nth-child(2) {
      margin-top: 35px;
    }
  }
}
