@import 'shared.scss';

$paddingTop: 50px;
$paddingBottom: 60px;
$horizontalSpace: 55px;

.heroTwo {
  display: flex;
  padding: $paddingTop 0 $paddingBottom 0;
}

.heroTwo > div {
  flex: 1 0 0; // allow to grow, not shrink and basis 0 for same width

  // center but don't wrap
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  // give space between
  margin-right: $horizontalSpace;

}

.heroTwo > div:last-child {
  // no space for last child
  margin-right: 0;
}


// RESPONSIVE
@include mqSmallerEqual ($bpXS)  {
  .heroTwo {
    // stack underneath each other
    display: block;
  }
  .heroTwo > div:first-child {
    // add space vertical, when stacked
    padding-bottom: 45px;
    // remove space horizontal, when stacked
    margin-right: 0;
  }
}

