@import 'shared.scss';

$paddingTop: 65px;
$paddingBottom: 55px;
$sectionSpacing: 100px;
$headingPadding: 1.5rem;

/* align footer content and footer logo section in flex */
.footer {
  display:flex;
  width: 100%;

  padding: $paddingTop 0 0 0;

  color: $colorGrey;
}

/* footer content can wrap */
.footerContent {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
}

.footerCaption {
  color: $colorY;
  margin-bottom: $headingPadding;
}
.footerSection {
  flex: 1 1 0; /* equal width sections */
  padding-right: $sectionSpacing;
  padding-bottom: $paddingBottom;
  @include fontText3;
  line-height: 1.6rem;
}

.footerLogo {
  flex-grow: 0;
  flex-shrink: 0;
  padding-bottom: $paddingBottom;

  /* move image and copyright text to the right */
  direction: rtl;

  img {
    width: 75px;
  }
}

// RESPONSIVE
@include mqSmallerEqual($bpS) {
  // wrap logo & copywright until the end
  .footer {
    flex-wrap: wrap;
  }

  // remove padding right
  .footerSection {
    padding-right: $sectionSpacing / 2;
  }

  // move image and copyright to the left
  .footerLogo {
    direction: ltr;
  }
}


