@import-normalize;
@import 'shared.scss';

html {
  font-size: 16px;

  @include mqSmallerEqual ($bpXS) {
    font-size: 14px;
  }
}

body {
  margin: 0;
  // Using Montserrat: https://fonts.google.com/specimen/Montserrat?selection.family=Montserrat
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

// Apply to all input elements since they don't inherit
input, select, textarea, button {
  font-family: inherit;
  font-weight: inherit;
  color: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* HEADINGS */

h1 {
  @include fontHeading1;
  margin: 0;
}
h2 {
  @include fontHeading2;
  margin: 0;
}
h3 {
  @include fontHeading3;
  margin: 0;
}
h4 {
  @include fontHeading4;
  margin: 0;
}

/* TEXT Elements */
p {
  @include fontText2;
  padding-bottom: 17px; // just looks better on all occurences
}

a {
  display: inline-block;
  color: $colorK;
  text-decoration: none; // we're underlining with border so we have more control

  margin-top: 4px; // move down so no visible baseline-change
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;

}

a:hover, a.isActive, a.hasUnderline {
  cursor: pointer; // for storybook, when we have no actual links
  border-color: $colorK;
}
a.hasNoUnderline {
  border-color: transparent !important;
}

a.isWhite {
  color: $colorWhite;
}
a.isWhite.hasUnderline {
  border-color: $colorWhite;
}
a.isWhite:hover, a.isWhite.isActive {
  color: $colorWhite;
  border-color: $colorWhite;
}

/* TEXT modifiers */
.g-isCentered {
  text-align: center;
}
.g-fontHeading1 {
  @include fontHeading1;
}
.g-fontHeading2 {
  @include fontHeading2;
}
.g-fontHeading3 {
  @include fontHeading3;
}
.g-fontHeading4 {
  @include fontHeading4;
}
.g-fontText1 {
  @include fontText1;
}
.g-fontText2 {
  @include fontText2;
}
.g-fontText3 {
  @include fontText3;
}
.g-fontBold {
  @include fontBold;
}

/* OTHER ELEMENTS */
hr {
  border-top: 1px solid $colorK;
}

/* STORYBOOK */
.g-storybookFontTable {
  width: 100%;
  td:first-child {
    vertical-align: top;
    width: 50px;
    padding-right: 10px;
  }
  td:last-child {
    padding-right: 0;
  }
}
.g-storybookColorBook {
  display: flex;
  flex-wrap: wrap;

  @include fontHeading2;
  color: $colorWhite;

  > div {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 50%;
    height: 400px;
  }
  .c {
    background-color: $colorC;
  }
  .m {
    background-color: $colorM;
  }
  .y {
    color: $colorK;
    background-color: $colorY;
  }
  .k {
    background-color: $colorK;
  }
  .grey {
    background-color: $colorGrey;
  }
  .greyLight {
    color: $colorK;
    background-color: $colorGreyLight;
  }
  .white {
    color: $colorK;
    background-color: $colorWhite;
  }
}
