@import 'shared.scss';

$fontColor: $colorWhite;
$backgroundColor: $colorK;
$fontColorInverted: $colorK;
$backgroundColorInverted: $colorWhite;
$stripeWidthTransparent: 5px;
$stripeWidthBackground: 2px;
$shadowOffsetTop: 9px;
$shadowOffsetLeft: 12px;
$shadowOffsetTopSmall: 4px;
$shadowOffsetLeftSmall: 6px;
$translateX: 4px;
$translateY: 4px;

/*
  remove active outline from buttons
  https://stackoverflow.com/questions/23333231/bootstrap-button-shows-blue-outline-when-clicked
*/
button:focus, button:active {
  outline: none;
}


/*
  container to prevent button from jumping
  on edge case
  when it's translating itself out of the mouse position
*/
.buttonContainer {
  position: relative;
  display:inline-block;
  cursor: pointer;

  // add margin to fix positioning visually by compensating for  absolutely positioned shadow
  margin: 0 $shadowOffsetLeft $shadowOffsetTop 0;

  z-index: 0; /* to allow z-index: -1 on shadow */
}

/*
  translate on hover
*/
.buttonContainer:hover button {
  transform: translate($translateX, $translateY);
}

.button {
  cursor: pointer;

  @include fontText2;
  @include fontBold;

  padding: 0.75rem 1.5rem;
  border:2px solid $backgroundColor;

  color: $fontColor;
  background-color: $backgroundColor;
}


/*
  striped shadow
*/
.buttonContainer:after {
  position:absolute;
  display:block;
  content: "";
  top: $shadowOffsetTop;
  left: $shadowOffsetLeft;
  width: 100%;
  height: 100%;
  /*
    so shadow doesn't grow larger with border
    than button without border
  */
  box-sizing: border-box;

  border: 2px solid $backgroundColor;

  /*
    stripes
    https://css-tricks.com/stripes-css/
  */
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent $stripeWidthTransparent,
    $backgroundColor $stripeWidthTransparent,
    $backgroundColor $stripeWidthTransparent + $stripeWidthBackground
  );

  z-index: -1; /* move behind button */
}

/*
  MODIFIERS
*/
/* inverted */
.isInverted {

  .button {
    color: $fontColorInverted;
    background-color: $backgroundColorInverted;
  }

  &:after {
    background: repeating-linear-gradient(
      45deg,
      $backgroundColorInverted,
      $backgroundColorInverted $stripeWidthTransparent,
      $backgroundColor $stripeWidthTransparent,
      $backgroundColor $stripeWidthTransparent + $stripeWidthBackground
    );
  }
}
/* text color */
// is !important to override .isInverted color
.hasYellowText button {
  color: $colorY !important;
}
.hasCyanText button {
  color: $colorC !important;
}
.hasMagentaText button {
  color: $colorM !important;
}
.hasWhiteText button {
  color: $colorWhite !important;
}
.hasBlackText button {
  color: $colorK !important;
}
/* BACKGROUND MODIFIERS */
.hasYellowBackground button {
  background-color: $colorY !important;
}
.hasCyanBackground button {
  background-color: $colorC !important;
}
.hasMagentaBackground button {
  background-color: $colorM !important;
}

// shadow
.hasCloserShadow {
  margin: 0 $shadowOffsetLeftSmall $shadowOffsetTopSmall 0;
  &:after {
    top: $shadowOffsetTopSmall;
    left: $shadowOffsetLeftSmall;
  }
}

.doesHideShadow button:hover {
  transform: translate($shadowOffsetLeft, $shadowOffsetTop);
}
.doesTransition button {
  transition: transform 0.1s ease;
}
.doesTransitionSlowly button {
  transition: transform 0.3s ease;
}
.doesAnimateShadow {
  &:hover > button {
    transform: none;
  }
  &:after {
    transition: transform 0.3s ease;
  }
  &:hover:after {
    transform: translate(-4px, -3px);
  }
  &.doesHideShadow:hover:after {
    transform: translate(-$shadowOffsetLeft, -$shadowOffsetTop);
  }
}
