/** TEXT **/
/*
  remove active outline from buttons
  https://stackoverflow.com/questions/23333231/bootstrap-button-shows-blue-outline-when-clicked
*/
button:focus, button:active {
  outline: none; }

/*
  container to prevent button from jumping
  on edge case
  when it's translating itself out of the mouse position
*/
.buttonContainer {
  position: relative;
  display: inline-block;
  cursor: pointer;
  margin: 0 12px 9px 0;
  z-index: 0;
  /* to allow z-index: -1 on shadow */ }

/*
  translate on hover
*/
.buttonContainer:hover button {
  transform: translate(4px, 4px); }

.button {
  cursor: pointer;
  font-size: 1.125rem;
  font-weight: 500;
  font-weight: 600;
  padding: 0.75rem 1.5rem;
  border: 2px solid #000000;
  color: #fff;
  background-color: #000000; }

/*
  striped shadow
*/
.buttonContainer:after {
  position: absolute;
  display: block;
  content: "";
  top: 9px;
  left: 12px;
  width: 100%;
  height: 100%;
  /*
    so shadow doesn't grow larger with border
    than button without border
  */
  box-sizing: border-box;
  border: 2px solid #000000;
  /*
    stripes
    https://css-tricks.com/stripes-css/
  */
  background: repeating-linear-gradient(45deg, transparent, transparent 5px, #000000 5px, #000000 7px);
  z-index: -1;
  /* move behind button */ }

/*
  MODIFIERS
*/
/* inverted */
.isInverted .button {
  color: #000000;
  background-color: #fff; }

.isInverted:after {
  background: repeating-linear-gradient(45deg, #fff, #fff 5px, #000000 5px, #000000 7px); }

/* text color */
.hasYellowText button {
  color: #fff500 !important; }

.hasCyanText button {
  color: #00a5ff !important; }

.hasMagentaText button {
  color: #ff00a5 !important; }

.hasWhiteText button {
  color: #fff !important; }

.hasBlackText button {
  color: #000000 !important; }

/* BACKGROUND MODIFIERS */
.hasYellowBackground button {
  background-color: #fff500 !important; }

.hasCyanBackground button {
  background-color: #00a5ff !important; }

.hasMagentaBackground button {
  background-color: #ff00a5 !important; }

.hasCloserShadow {
  margin: 0 6px 4px 0; }
  .hasCloserShadow:after {
    top: 4px;
    left: 6px; }

.doesHideShadow button:hover {
  transform: translate(12px, 9px); }

.doesTransition button {
  transition: transform 0.1s ease; }

.doesTransitionSlowly button {
  transition: transform 0.3s ease; }

.doesAnimateShadow:hover > button {
  transform: none; }

.doesAnimateShadow:after {
  transition: transform 0.3s ease; }

.doesAnimateShadow:hover:after {
  transform: translate(-4px, -3px); }

.doesAnimateShadow.doesHideShadow:hover:after {
  transform: translate(-12px, -9px); }
