@import 'shared.scss';

$verticalPadding: 55px;
$horizontalSpace: 30px;

.heroThree {
  display: flex;
  padding: $verticalPadding 0 $verticalPadding 0;
}

.heroThree > div {
  flex: 1 0 0; // allow to grow, not shrink and basis 0 for same width

  // center but don't wrap
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  // give space between
  padding-right: $horizontalSpace;
  padding-left: $horizontalSpace;

  border-right: 1px solid $colorWhite;

  img {
    max-width: 100%;
    margin-top: -45px;
  }

}
.heroThree > div:first-child {
  padding-right: $horizontalSpace;
  padding-left: 0;
}

.heroThree > div:last-child {
  // no space for last child
  padding-right: 0;
  // remove border
  border-right: 0;
}


// RESPONSIVE
@include mqSmallerEqual ($bpXS)  {
  .heroThree {
    // stack underneath each other
    display: block;
    // remove top padding, because we don't have lines anymore
    padding-top:0px;

    & > div {
      width: 100%;

      // add space vertical, when stacked
      padding-bottom: 15px !important;
      // remove space horizontal, when stacked
      margin-right: 0;

      // remove left/right paddings;
      padding: 0;
      // remove border
      border-right: 0;
    }
  }
}

