@import 'shared.scss';

$verticalPadding: 25px; /* is the responsibility of the child component, should it?! */
$VerticalPaddingLarge: 55px;

$paddingBreakOut: 0px;
$paddingWide: 40px;
$paddingDefault: 80px;
$paddingNarrow: 120px;
$paddingM: 50px;
$paddingS: 40px;
$paddingXS: 30px;

.contentContainer {
  min-height: 25px;
  overflow: hidden;
  max-width: 100%;
  box-sizing: border-box;

  padding: 0 $paddingDefault;

  background-color: $colorWhite;
}

/* MODIFIERS PADDING */
.hasVerticalPadding {
  padding-top: $verticalPadding;
  padding-bottom: $verticalPadding;
}
.hasVerticalPaddingLarge {
  padding-top: $VerticalPaddingLarge;
  padding-bottom: $VerticalPaddingLarge;
}

/* MODIFIERS WIDTHX */
.isBreakOut {
  padding-left: $paddingBreakOut;
  padding-right: $paddingBreakOut;
}
.isWide {
  padding-left: $paddingWide;
  padding-right: $paddingWide;
}
.isDefaultWidth {
  padding-left: $paddingDefault;
  padding-right: $paddingDefault;
}
.isNarrow {
  padding-left: $paddingNarrow;
  padding-right: $paddingNarrow;
}


@include mqSmallerEqual($bpM) {
  .contentContainer {
    padding-left: $paddingM;
    padding-right: $paddingM;
    &.isBreakOut {
      padding: 0;
    }
  }
}


@include mqSmallerEqual($bpS) {
  .contentContainer {
    padding-left: $paddingS;
    padding-right: $paddingS;
    &.isBreakOut {
      padding: 0;
    }
  }
}

@include mqSmallerEqual($bpXS) {
  .contentContainer {
    padding-left: $paddingXS;
    padding-right: $paddingXS;
  }
}

/* MODIFIERS BACKDROP */
.isWhite {
  background-color: $colorWhite;
}
.isGrey {
  background-color: $colorGreyLight;
}
.isBlack {
  // set good default color props based on background color
  color: $colorWhite;
  background-color: $colorK;

  hr {
    border-top: 1px solid $colorWhite;
  }
}
