.enter {
  opacity: 0;
  transform: translate(5vw, 0);
}

.enterActive {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 300ms linear;
}

.exit {
  opacity: 1;
  transform: translate(0, 0);
}

.exitActive {
  transform: translate(5vw, 0);
  opacity: 0;
  transition: all 300ms linear;
}
