
/** FONTS **/
// each font style contains font family, size and weight
// get smaller from top to bottom
// Using Montserrat: https://fonts.google.com/specimen/Montserrat?selection.family=Montserrat
// Medium (500) and Semi-Bold (600)


/** HEADINGS **/
// large heading: e.g slider
@mixin fontHeading1 {
  // 58px, semi-bold
  font-size: 3.625rem;
  font-weight: 600;
}
// medium heading: e.g. WÃ¤hlen sie ihr Produkt
@mixin fontHeading2 {
  // 38px, semi-bold
  font-size: 2.375rem;
  font-weight: 600;
}
// smaller heading: e.g HeroThree: Einfach und qualitativ
@mixin fontHeading3 {
  // 26px, semi-bold
  font-size: 1.625rem;
  font-weight: 600;
}
// even smaller heading: e.g. Footer
@mixin fontHeading4 {
  // 22 px, semi-bold
  font-size: 1.375rem;
  font-weight: 600;
}


/** TEXT **/
// larger text: e.g. slider right
@mixin fontText1 {
  // 22px, medium (oder semi bold)
  font-size: 1.375rem;
  font-weight: 500;
}
// medium text: e.g. slider left, buttons
@mixin fontText2 {
  // 18px, medium
  font-size: 1.125rem;
  font-weight: 500;
}
// small text: e.g. ProductGrid
@mixin fontText3 {
  // 16px, medium
  font-size: 1rem;
  font-weight: 500;
}
// allow to set bold font in every case
@mixin fontBold {
  font-weight: 600;
}
