@import 'shared.scss';

$paddingTop: 35px;
$paddingBottom: 45px;
$itemSpacing: 60px;

/* PRODUCTGRID */
.productGrid {
  padding: $paddingTop 0 $paddingBottom 0;

  h2 {
    margin-top: 40px;
    margin-bottom: 20px;
    text-align: center;
  }

}

.categoriesContainer {
  display: flex;
  flex-wrap: wrap;
}

.category {
  // width: 220px;
  flex: 0 1 220px;

  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: center;



  margin: 20px 0;

  img {
    width: 120px;
  }
}
