@import 'shared.scss';

$verticalPadding: 40px;

/* HEADER */
.header {
  display: flex;
  padding: $verticalPadding 0;

  @include fontText2;
}

/* LEFT, CENTER, RIGHT */
.left, .center, .right {
  flex: 1 0 0px;
  display: flex;
  align-items: center;
  text-align: center;
}

.right {
  a:not(:last-child) {
    margin: 0px 15px;
  }
  a:last-child {
    margin-left: 15px;
  }
}

.left {
  justify-content: flex-start;
  // background: skyblue;
}

.center {
  flex-basis: 150px;
  justify-content: center;
  // background: blue;

  a {
    margin: 0px 15px;
  }
}

@include mqSmallerEqual($bpL) {
  .left {flex-basis:150px}
  .center {flex-basis: 290px; flex-shrink: 1;}
}
@include mqSmallerEqual($bpM) {
  .center {flex-wrap: wrap; @include fontText3;}
}
@include mqSmallerEqual($bpXS) {
  .left, .right {
    flex-basis: 30px;
    max-height: 40px;
    max-width: 40px;

  }
  .left {
    img {
      margin-left: -15px;
    }
  }
}

.right {
  justify-content: flex-end;
  // background: blueviolet;
}

/* HEADER CONTENT */
.logo {
  width: 170px;
}
.logoSmall {
  width: 60px;
}
.burger {
  width: 30px;
}
.close {
  width: 30px;
}

/* NAV */
.nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;

  color: $colorWhite;

  background: $colorK;
  overflow: auto;
  z-index: 1;

  h2 {
    padding-bottom: 50px;
  }
}
.navIcon {
  padding-top: $verticalPadding;
  display: flex;
  justify-content: flex-end;
}
.content {
  display: flex;
  flex-wrap: wrap;
  max-width: 800px;

  > div {
    width: 200px;
    margin-bottom: 5px;
  }
  padding-bottom: $verticalPadding;
}

/* ANIMATIONS */
.enter {
  opacity: 0;
  transition: opacity 2500ms ease-in;
}
.enterActive {
  opacity: 1;
}
