@import 'shared.scss';

$bubbleSize: 14px;
$bubbleSpacing: 5px;
$bubbleMargin: 17px;

$bubbleSizeSmall: 12px;
$bubbleSpacingSmall: 4px;
$bubbleMarginSmall: 14px;

/* SLIDER */
.slider {
  width: 100%;
}

.slideBox {
  display: flex;
  flex-wrap: nowrap;

  user-select: none;

  height: 580px;
  background: skyblue;
  transition: transform 400ms ease-in-out;
}

/* SLIDE */
.slide {
  flex: 0 0 auto;
  padding: 30px;
  box-sizing: border-box;
  width: 50vw;
  height: 100%;
  overflow: hidden;
}
@include mqSmallerEqual($bpM) {
  .slideBox { height: 400px }
  .slide { width: 100% }
}
@include mqSmallerEqual($bpXS) {
  .slideBox { height: 300px }
}

/* SLIDE MODIFIERS COLORS */
.isC {
  background-color: $colorC;
}
.isM {
  background-color: $colorM;
}
.isY {
  background-color: $colorY;
}
.isK {
  color: $colorWhite;
  background-color: $colorK;
}
.isWhite {
  background-color: $colorWhite;
}
/* SLIDE MODIFIERS CONTENT */
.isCenter {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-left: 7%;
  padding-right: 7%;
  & > div {

    text-align: center;
  }
}



/* BUBBLES */
.bubblesContainer {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  margin-top: $bubbleMargin;
}

.bubble {
  cursor: pointer;

  width: $bubbleSize;
  height: $bubbleSize;
  margin: $bubbleSpacing;

  border: 1px solid $colorK;

  background-color: transparent;
  transition: background-color 350ms ease;
}
@include mqSmallerEqual($bpM) {
  .bubble {
    width: $bubbleSizeSmall;
    height: $bubbleSizeSmall;
    margin: $bubbleSpacingSmall;
  }
}

.isActive {
  background-color: $colorK;
}

